"use client"
import {
  Home, Ruler, BarChart3, FileDown, FileText, Settings, Leaf
} from "lucide-react"

const menuItems = [
  { icon: Home, label: "Home", id: "home" },
  { icon: Ruler, label: "Measure", id: "measure" },
  { icon: BarChart3, label: "Analyze", id: "analyze" },
  { icon: FileDown, label: "Reduce", id: "reduce" },
  { icon: FileText, label: "Report", id: "report" },
]

export function Sidebar({ activeItem, setActiveItem }: {
  activeItem: string,
  setActiveItem: (id: string) => void
}) {
  return (
    <div className="h-full bg-zinc-800 rounded-r-3xl w-20 flex flex-col items-center py-8 space-y-6">
      <div className="bg-white/20 p-3 rounded-full">
        <Leaf className="text-white w-6 h-6" />
      </div>
      {menuItems.map((item) => {
        const Icon = item.icon
        const isActive = item.id === activeItem
        return (
          <button
            key={item.id}
            onClick={() => setActiveItem(item.id)}
            className={`
              p-3 rounded-full transition-all
              ${isActive ? "bg-white text-zinc-800 shadow-lg" : "text-white hover:bg-white/10"}
            `}
          >
            <Icon className="w-5 h-5" />
          </button>
        )
      })}
      <div className="mt-auto mb-4">
        <Settings className="text-white w-5 h-5" />
      </div>
    </div>
  )
}
